body:after {
  position: absolute;
  bottom: 10%;
  right: 0;
  content: url("../img/footer__shape.png");
  pointer-events: none;
}

body:before {
  position: absolute;
  top: 0;
  left: 0;
  content: url("../img/header__shape.png");
  transform: scaleX(1.8);
  pointer-events: none;
}

.header {
  margin: 0 0 20px;
  padding-top: 15px;
  padding-bottom: 10px;
  box-shadow: 0px 6px 24px 0 rgba(0, 0, 0, 0.06);
  background: rgba(255, 255, 255, 0.53);
  position: relative;
  z-index: 2;
}

.header__logo {
  position: relative;
  top: -21px;
}

.header__inner {
  margin-left: 108px;
}

.header__caption {
  margin-bottom: 12px;
}

.header__caption h1 {
  font: 400 35px MyriadPro;
  margin-right: 20px;
  color: #262626;
  letter-spacing: -1.4px;
}

.header__caption span {
  padding: 6px 11px;
  color: #4090db;
  border: 1px solid #4090db;
  border-radius: 5px;
  font: 400 15px HelveticaNeue;
  letter-spacing: -0.6px;
}

.header__stat div {
  margin-right: 24px;
  font-size: 17px;
  letter-spacing: 0.34px;
}

.header__dateSelect {
  margin-top: 20px;
  display: flex;
  align-items: center;
}

.header__dateSelect span {
  margin-right: 14px;
  font: 400 17px Tahoma;
}

.header__dateSelect-holder {
  position: relative;
  margin-right: 64px;
}

.header__dateSelect-holder input {
  width: 143px;
  height: 32px;
  border: solid 1px #cccccc;
  border-radius: 5px;
  padding-left: 10px;
  color: #333333;
  font: 400 14px HelveticaNeue;
}

.header__dateSelect-holder input::-webkit-input-placeholder {
  color: #333333;
  font: 400 14px HelveticaNeue;
}

.header__dateSelect-holder input::-moz-placeholder {
  color: #333333;
  font: 400 14px HelveticaNeue;
}

.header__dateSelect-holder input:-ms-input-placeholder {
  color: #333333;
  font: 400 14px HelveticaNeue;
}

.header__dateSelect-button {
  position: absolute;
  z-index: 1;
  height: 32px;
  width: 32px;
  background: #ebebeb;
  border: solid 1px #cccccc;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
}

.header__dateSelect-button.select {
  border-left: none;
  right: -30%;
  border-bottom-right-radius: 5px;
  border-top-right-radius: 5px;
}

.header__dateSelect-button.close {
  right: -11px;
}

.catalog {
  max-width: 1025px;
  margin: 0 auto;
  margin-bottom: 40px;
  position: relative;
  z-index: 1;
}

.catalog .more {
  margin-top: 45px;
  border-radius: 50px;
  border: solid 1px #000000;
  background-color: #fafafa;
  font: 400 12px ProximaNova;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 2;
  width: 124px;
}

.catalog__caption {
  margin-bottom: 20px;
  display: flex;
  justify-content: flex-end;
}

.catalog__caption form {
  padding-left: 195px;
  margin: auto;
}

.catalog__options {
  display: flex;
}

.catalog__options a {
  margin-left: 24px;
}

.catalog__options a img {
  filter: grayscale(1);
  opacity: 0.6;
  transition: ease-in-out 0.3s all;
}

.catalog__options a.active img {
  filter: grayscale(0);
  opacity: 1;
}

.catalog__list[data-attr="rows"] {
  max-width: 936px;
  margin: 0 0 0 auto;
}

.catalog__list[data-attr="rows"] .catalog__item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.catalog__list[data-attr="rows"] .catalog__item > * {
  flex: 0 1 25%;
  padding-top: 15px;
}

.catalog__list[data-attr="rows"] .catalog__item-img {
  padding-top: 0;
  max-width: 95px;
  max-height: 95px;
  margin-right: -16px;
}

.catalog__list[data-attr="rows"] .catalog__item-img img {
  object-fit: cover;
}

.catalog__list[data-attr="rows"] .catalog__item-today p,
.catalog__list[data-attr="rows"] .catalog__item-previous p {
  color: #343537;
  font: 600 17px ProximaNova;
  margin-bottom: 8px;
}

.catalog__list[data-attr="rows"] .catalog__item-previous {
  padding-left: 13px;
}

.catalog__list[data-attr="rows"] .catalog__item-previous div div {
  margin-right: 30px;
}

.catalog__list[data-attr="rows"] .catalog__item-stats div {
  color: #343537;
  display: flex;
  font: 700 17px/1.41 ProximaNova;
  align-items: center;
  margin-right: 22px;
}

.catalog__list[data-attr="rows"] .catalog__item-stats div:hover {
  cursor: pointer;
}

.catalog__list[data-attr="rows"] .catalog__item-stats div:hover img {
  opacity: 0.6;
}

.catalog__list[data-attr="rows"] .catalog__item-stats img {
  margin-right: 6px;
  filter: invert(1);
  transition: ease-in-out 0.3s all;
  opacity: 1;
}

.catalog__list[data-attr="rows"] .catalog__item-uploadDate {
  padding-left: 36px;
}

.catalog__list[data-attr="rows"] .catalog__item-uploadDate p {
  font: 700 17px HelveticaNeue;
  color: #343537;
  margin-bottom: 10px;
}

.catalog__list[data-attr="tiles"] {
  max-width: 936px;
  flex-wrap: wrap;
  display: flex;
  margin: 0 0 0 auto;
  justify-content: space-between;
}

.catalog__list[data-attr="tiles"] .catalog__item {
  flex: 0 1 31.5%;
  position: relative;
  margin-bottom: 30px;
}

.catalog__list[data-attr="tiles"] .catalog__item:nth-of-type(1) .catalog__item-previous,
.catalog__list[data-attr="tiles"] .catalog__item:nth-of-type(2) .catalog__item-previous,
.catalog__list[data-attr="tiles"] .catalog__item:nth-of-type(3) .catalog__item-previous {
  bottom: 20.1%;
  margin-left: 3px;
}

.catalog__list[data-attr="tiles"] .catalog__item:nth-of-type(1) .catalog__item-today,
.catalog__list[data-attr="tiles"] .catalog__item:nth-of-type(2) .catalog__item-today,
.catalog__list[data-attr="tiles"] .catalog__item:nth-of-type(3) .catalog__item-today {
  top: initial;
  bottom: 34%;
  margin-left: 3px;
}

.catalog__list[data-attr="tiles"] .catalog__item-img {
  position: relative;
  display: block;
}

.catalog__list[data-attr="tiles"] .catalog__item-img:before {
  content: '';
  position: absolute;
  z-index: 1;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.7);
}

.catalog__list[data-attr="tiles"] .catalog__item-img[data-rel="lightcase"]:after {
  content: url("../img/photo_img.png");
  position: absolute;
  z-index: 1;
  top: 10px;
  right: 10px;
}

.catalog__list[data-attr="tiles"] .catalog__item-img img {
  width: 100%;
}

.catalog__list[data-attr="tiles"] .catalog__item-today,
.catalog__list[data-attr="tiles"] .catalog__item-previous {
  position: absolute;
  top: 41.4%;
  padding-left: 10px;
  z-index: 1;
  color: #fff;
  display: flex;
  left: 20px;
  right: 20px;
  justify-content: space-between;
  align-items: center;
  font: 600 17px ProximaNova;
}

.catalog__list[data-attr="tiles"] .catalog__item-stats {
  align-items: center;
}

.catalog__list[data-attr="tiles"] .catalog__item-stats div {
  display: flex;
  align-items: center;
  margin-right: 20px;
  width: 54px;
}

.catalog__list[data-attr="tiles"] .catalog__item-stats div img {
  margin-right: 5px;
}

.catalog__list[data-attr="tiles"] .catalog__item-previous {
  bottom: 37.5%;
  top: initial;
}

.catalog__list[data-attr="tiles"] .catalog__item-uploadDate {
  margin-left: 7px;
  position: absolute;
  bottom: 15px;
  left: 0;
  right: 0;
  z-index: 1;
  color: #ffffff;
  display: flex;
  justify-content: center;
}

.catalog__list[data-attr="tiles"] .catalog__item-uploadDate p {
  font: 600 17px ProximaNova;
}

.catalog__list[data-attr="tiles"] .catalog__item-uploadDate p:first-of-type {
  margin-right: 5px;
}

.catalog__list[data-attr="tiles"] .catalog__item-uploadDate p:first-of-type:after {
  content: ': ';
}

@media (max-width: 1800px) {
  body:before {
    transform: scaleX(1.3);
  }
}

@media (max-width: 1649px) {
  body:before {
    transform: scaleX(1);
  }
}

@media (max-width: 1480px) {
  body:before {
    top: -36px;
    left: -88px;
    transform: scale(0.9);
  }
}

@media (max-width: 1279px) {
  .catalog {
    max-width: 936px;
  }

  .catalog__list[data-attr=tiles] {
    margin: 0 auto;
  }

  body:before {
    top: -69px;
    left: -145px;
    transform: scale(0.7);
  }
}

@media (max-width: 1150px) {
  body:before {
    left: -200px;
  }
}

@media (max-width: 1023px) {
  body:before {
    top: -69px;
    left: -336px;
    transform: scale(0.7);
  }

  .header {
    padding: 15px;
  }

  .header__inner {
    margin-left: auto;
  }

  .catalog {
    padding: 0 15px;
  }

  .catalog__list[data-attr=tiles] .catalog__item {
    flex: 0 1 49%;
    margin-bottom: 13px;
  }

  .catalog__list[data-attr=tiles] .catalog__item:nth-of-type(3) .catalog__item-today {
    top: 41.4%;
    bottom: initial;
  }

  .catalog__list[data-attr=tiles] .catalog__item:nth-of-type(3) .catalog__item-previous {
    bottom: 37.5%;
  }
}

@media (max-width: 767px) {
  body:before {
    display: none;
  }

  .header {
    justify-content: space-between;
    align-items: center;
  }

  .header__inner {
    display: none;
    position: absolute;
    z-index: 2;
    left: 0;
    right: 0;
    top: 0;
    background: #ffffff;
    padding: 15px 15px 30px;
    box-shadow: 0px 6px 24px 0 rgba(0, 0, 0, 0.5);
  }

  .header__logo {
    top: initial;
    max-width: 50px;
  }

  .header__logo-lg {
    display: none;
  }

  .header__logo-xs {
    display: block !important;
  }

  .header__caption {
    display: block;
  }

  .header__caption h1 {
    margin-bottom: 10px;
  }

  .header__dateSelect {
    display: block;
  }

  .header__dateSelect span {
    display: block;
  }

  .header__dateSelect form {
    display: inline-block;
  }

  .header__dateSelect-holder {
    margin-bottom: 10px;
  }

  .header__stat {
    display: block;
  }

  .header.active .header__inner {
    display: block;
  }

  .catalog__options {
    display: none;
  }

  .catalog__caption {
    justify-content: center;
  }

  .catalog__caption form {
    margin: 0;
    padding: 0;
  }

  .menu {
    display: flex !important;
    position: relative;
    z-index: 5;
  }
}

@media (max-width: 660px) {
  .catalog {
    max-width: 450px;
  }

  .catalog__list[data-attr=tiles] .catalog__item {
    max-width: 450px;
    margin: 0 auto 10px;
    flex: 0 1 100%;
  }

  .catalog__list[data-attr=tiles] .catalog__item:nth-of-type(1) .catalog__item-today,
  .catalog__list[data-attr=tiles] .catalog__item:nth-of-type(2) .catalog__item-today {
    top: 41.4%;
    bottom: initial;
  }

  .catalog__list[data-attr=tiles] .catalog__item:nth-of-type(1) .catalog__item-previous,
  .catalog__list[data-attr=tiles] .catalog__item:nth-of-type(2) .catalog__item-previous {
    bottom: 37.5%;
  }
}