@charset "UTF-8";

@font-face {
  font-family: 'ProximaNova';
  src: url('../fonts/ProximaNova-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal; }
@font-face {
  font-family: 'ProximaNova';
  src: url('../fonts/ProximaNova-Bold.ttf') format('truetype');
  font-weight: 700;
  font-style: normal; }
@font-face {
  font-family: 'ProximaNova';
  src: url('../fonts/ProximaNova-Semibold.ttf') format('truetype');
  font-weight: 600;
  font-style: normal; }
@font-face {
  font-family: 'HelveticaNeue';
  src: url('../fonts/HelveticaNeue-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal; }
@font-face {
  font-family: 'MyriadPro';
  src: url('../fonts/MyriadPro-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal; }
@font-face {
  font-family: 'HelveticaNeueCE';
  src: url('../fonts/HelveticaNeueCE-Bold.ttf') format('truetype');
  font-weight: 700;
  font-style: normal; }

@font-face {
  font-family: 'Tahoma';
  src: url('../fonts/Tahoma-Regular.ttf') format('truetype');
  font-weight: 700;
  font-style: normal; }

@import "typical.sass";
$Tahoma : Tahoma;
$HelveticaNeueCE : HelveticaNeueCE;
$ProximaNova : ProximaNova;
$HelveticaNeue : HelveticaNeue;
$MyriadPro : MyriadPro;
$tt: ease-in-out 0.3s all;
$blue: #4090db;
body {
  &:after {
    position: absolute;
    bottom: 10%;
    right: 0;
    content: url("../img/footer__shape.png");
    pointer-events: none; }
  &:before {
    position: absolute;
    top: 0;
    left: 0;
    content: url("../img/header__shape.png");
    transform: scaleX(1.8);
    pointer-events: none; } }
.header {
  margin: 0 0 20px;
  padding-top: 15px;
  padding-bottom: 10px;
  box-shadow: 0px 6px 24px 0 rgba(0, 0, 0, 0.06);
  background: rgba(255, 255, 255, 0.53);
  position: relative;
  z-index: 2;
  &__logo {
    position: relative;
    top: -21px; }
  &__inner {
    margin-left: 108px; }
  &__caption {
    margin-bottom: 12px;
    h1 {
      font: 400 35px $MyriadPro;
      margin-right: 20px;
      color: #262626;
      letter-spacing: -1.4px; }
    span {
      padding: 6px 11px;
      color: $blue;
      border: 1px solid $blue;
      border-radius: 5px;
      font: 400 15px $HelveticaNeue;
      letter-spacing: -0.6px; } }
  &__stat {
    div {
      margin-right: 24px;
      font-size: 17px;
      letter-spacing: 0.34px; } }
  &__dateSelect {
    margin-top: 20px;
    display: flex;
    align-items: center;
    span {
      margin-right: 14px;
      font: 400 17px $Tahoma; }
    &-holder {
      position: relative;
      margin-right: 64px;
      input {
        width: 143px;
        height: 32px;
        border: solid 1px #cccccc;
        border-radius: 5px;
        padding-left: 10px;
        color: #333333;
        font: 400 14px $HelveticaNeue;
        &::-webkit-input-placeholder {
          color: #333333;
          font: 400 14px $HelveticaNeue; }
        &::-moz-placeholder {
          color: #333333;
          font: 400 14px $HelveticaNeue; }

        &:-ms-input-placeholder {
          color: #333333;
          font: 400 14px $HelveticaNeue; } } }

    &-button {
      position: absolute;
      z-index: 1;
      height: 32px;
      width: 32px;
      background: #ebebeb;
      border: solid 1px #cccccc;
      display: flex;
      align-items: center;
      justify-content: center;
      top: 0;
      &.select {
        border-left: none;
        right: -30%;
        border-bottom-right-radius: 5px;
        border-top-right-radius: 5px; }
      &.close {
        right: -11px; } } } }
.catalog {
  max-width: 1025px;
  margin: 0 auto;
  margin-bottom: 40px;
  position: relative;
  z-index: 1;

  .more {
    margin-top: 45px;
    border-radius: 50px;
    border: solid 1px #000000;
    background-color: #fafafa;
    font: 400 12px $ProximaNova;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    justify-content: center;
    line-height: 2;
    width: 124px; }


  &__caption {
    margin-bottom: 20px;
    display: flex;
    justify-content: flex-end;
    form {
      padding-left: 195px;
      margin: auto; } }
  &__options {
    display: flex;
    a {
      margin-left: 24px;
      img {
        filter: grayscale(1);
        opacity: 0.6;
        transition: $tt; }
      &.active {
        img {
          filter: grayscale(0);
          opacity: 1; } } } }
  &__list[data-attr="rows"] {
    max-width: 936px;
    margin: 0 0 0 auto;
    .catalog__item {
      display: flex;
      justify-content: space-between;
      margin-bottom: 10px;
      & > * {
        flex: 0 1 25%;
        padding-top: 15px; }
      &-img {
        padding-top: 0;
        max-width: 95px;
        max-height: 95px;
        margin-right: -16px;
        img {
          object-fit: cover; } }
      &-today, &-previous {
        p {
          color: #343537;
          font: 600 17px $ProximaNova;
          margin-bottom: 8px; } }
      &-previous {
        padding-left: 13px;
        div {
          div {
            margin-right: 30px; } } }
      &-stats {
        div {
          color: #343537;
          display: flex;
          font: 700 17px / 1.41 $ProximaNova;
          align-items: center;
          margin-right: 22px;
          &:hover {
            img {
              opacity: 0.6; }
            cursor: pointer; } }
        img {
          margin-right: 6px;
          filter: invert(1);
          transition: $tt;
          opacity: 1; } }
      &-uploadDate {
        padding-left: 36px;
        p {
          font: 700 17px $HelveticaNeue;
          color: #343537;
          margin-bottom: 10px; } } } }
  &__list[data-attr="tiles"] {
    max-width: 936px;
    flex-wrap: wrap;
    display: flex;
    margin: 0 0 0 auto;
    justify-content: space-between;
    .catalog__item {
      flex: 0 1 31.5%;
      position: relative;
      margin-bottom: 30px;

      &:nth-of-type(1), &:nth-of-type(2), &:nth-of-type(3) {
        .catalog__item-previous {
          bottom: 20.1%;
          margin-left: 3px; }
        .catalog__item-today {
          top: initial;
          bottom: 34%;
          margin-left: 3px; } }

      &-img {
        position: relative;
        display: block;

        &:before {
          content: '';
          position: absolute;
          z-index: 1;
          top: 0;
          bottom: 0;
          right: 0;
          left: 0;
          background: rgba(0,0,0,0.7); }
        &[data-rel="lightcase"] {
          &:after {
            content: url("../img/photo_img.png");
            position: absolute;
            z-index: 1;
            top: 10px;
            right: 10px; } }
        img {
          width: 100%; } }
      //&-uploadDate
      //position: absolute
      //z-index: 1
      //bottom: 10px
      //left: 0
      //right: 0
      //color: #ffffff
      //text-align: center
      //display: flex
      &-today, &-previous {
        position: absolute;
        top: 41.4%;
        padding-left: 10px;
        z-index: 1;
        color: #fff;
        //transform: translateY(100%)
        display: flex;
        left: 20px;
        right: 20px;
        justify-content: space-between;
        align-items: center;
        font: 600 17px $ProximaNova; }
      &-stats {
        align-items: center;
        div {
          display: flex;
          align-items: center;
          margin-right: 20px;
          width: 54px;
          img {
            margin-right: 5px; } } }
      &-previous {
        bottom: 37.5%;
        top: initial;
 }        //transform: translateY(-100%)

      &-uploadDate {
        margin-left: 7px;
        position: absolute;
        bottom: 15px;
        left: 0;
        right: 0;
        z-index: 1;
        color: #ffffff;
        display: flex;
        justify-content: center;
        p {
          font: 600 17px $ProximaNova;
          &:first-of-type {
            margin-right: 5px;
            &:after {
              content: ': '; } } } } } } }
@media (max-width: 1800px) {
  body:before {
    transform: scaleX(1.3); } }
@media (max-width: 1649px) {
  body:before {
    transform: scaleX(1); } }

@media (max-width: 1480px) {
  body:before {
    top: -36px;
    left: -88px;
    transform: scale(0.9); } }
@media (max-width: 1279px) {
  .catalog {
    max-width: 936px;
    &__list[data-attr=tiles] {
      margin: 0 auto; } }
  body:before {
    top: -69px;
    left: -145px;
    transform: scale(0.7); } }
@media (max-width: 1150px) {
  body:before {
    left: -200px; } }
@media (max-width: 1023px) {
  body:before {
    top: -69px;
    left: -336px;
    transform: scale(0.7); }
  .header {
    padding: 15px;
    &__inner {
      margin-left: auto; } }
  .catalog {
    padding: 0 15px;
    &__list[data-attr=tiles] {
      .catalog__item {
        flex: 0 1 49%;
        margin-bottom: 13px;
        &:nth-of-type(3) {
          .catalog__item-today {
            top: 41.4%;
            bottom: initial; }
          .catalog__item-previous {
            bottom: 37.5%; } } } } } }
@media (max-width: 767px) {
  body:before {
    display: none; }
  .header {
    justify-content: space-between;
    align-items: center;
    &__inner {
      display: none;
      position: absolute;
      z-index: 2;
      left: 0;
      right: 0;
      top: 0;
      background: #ffffff;
      padding: 15px 15px 30px;
      box-shadow: 0px 6px 24px 0 rgba(0, 0, 0, 0.5); }

    &__logo {
      top: initial;
      max-width: 50px;
      &-lg {
        display: none; }
      &-xs {
        display: block!important; } }
    &__caption {
      display: block;
      h1 {
        margin-bottom: 10px; } }
    &__dateSelect {
      display: block;
      span {
        display: block; }
      form {
        display: inline-block; }
      &-holder {
        margin-bottom: 10px; } }
    &__stat {
      display: block; }
    &.active {
      .header__inner {
        display: block; } } }
  .catalog {
    &__options {
      display: none; }
    &__caption {
      justify-content: center;
      form {
        margin: 0;
        padding: 0; } } }
  .menu {
    display: flex!important;
    position: relative;
    z-index: 5; } }

@media (max-width: 660px) {
  .catalog {
    max-width: 450px;
    &__list[data-attr=tiles] {
      .catalog__item {
        max-width: 450px;
        margin: 0 auto 10px;
        flex: 0 1 100%;
        &:nth-of-type(1), &:nth-of-type(2) {
          .catalog__item-today {
            top: 41.4%;
            bottom: initial; }
          .catalog__item-previous {
            bottom: 37.5%; } } } } } }
