$tt: 'ease-in-out 0.3s all';
$blue: #4090db;
body,html {
  background: #ffffff;
  font: 400 16px 'ProximaNova';
  color: #1f373d;
  font-size: 16px;
  line-height: 1.41;
  max-width: 1920px;
  width: 100%;
  overflow-x: hidden;
  margin: 0 auto;
  position: relative;
  &.locked, &.stopScroll {
    overflow: hidden!important;
 } }    //padding-right: 17px

.preloader {
  color: #e95822;
  font-size: 1.5rem;
  letter-spacing: 4px;
  transition: all ease-in-out 0.3s;
  -webkit-animation: letters 3s infinite linear;
  animation: letters 3s infinite linear; }

h1, h2, p {
  margin: 0; }
a {
 }  //color: #1f373d
html {
  &.active {
    overflow: hidden!important; } }
* {
  outline: none!important; }
.dn {
  display: none!important; }
.menu {
  display: none;
  transition: all 1s ease-in-out;
  margin-left: 15px;
  align-items: center;
  width: 44px;
  min-width: 44px;
  max-width: 44px;
  height: 44px;
  min-height: 44px;
  max-height: 44px;
  justify-content: center;
  background: #FFFFFF;
  border-radius: 100%;

  .sandwich {
    width: 16px;
    height: 13px;
    display: block;
    position: relative; }
  .line {
    height: 2px;
    background-color: $blue;
    border: 1px solid $blue;
    width: 100%;
    display: block;
    position: absolute;
    left: 0;
    transition: all 1s ease-in-out;
    &.line-1 {
      top: 0; }
    &.line-2 {
      top: 50%;
      transform: translateY(-50%); }
    &.line-3 {
      bottom: 0; } }
  &.active {
    .line {
      &.line-1 {
        transform: rotate(220deg);
        top: 50%; }
      &.line-2 {
        transform: rotate(-220deg);
        top: 50%; }
      &.line-3 {
        opacity: 0; } } } }
* {
  box-sizing: border-box;
  outline: none; }
input, button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  line-height: normal;
  &::-webkit-input-placeholder {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    line-height: normal; }
  &::-moz-placeholder {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    line-height: normal; }
  &:-ms-input-placeholder {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    line-height: normal; } }

button {
  &:hover {
    cursor: pointer; } }
a {
  text-decoration: none;
  outline: none;
  &:hover {
    cursor: pointer; } }
ul {
  padding: 0;
  margin: 0;
  list-style: none; }
img {
  max-width: 100%;
